import React from "react";
import { useHistory } from "react-router";
import { NavLink } from "react-router-dom";

export default function List({nama, nomor, type, ayat}){
    let history = useHistory()
    
    return(
        <React.Fragment>
           <NavLink to={`/alquran/surah/${nomor}`}>
                <div  className="flex justify-between px-7 py-7">
                    <div className="flex">
                        <div className="bg-green-400 mr-4 text-center w-14 rounded-xl">
                            <p className="mt-4 font-bold text-white">{nomor}</p>
                        </div>
                        <div>
                            <p className="text-2xl capitalize font-semibold">{nama}</p>
                            <p className="capitalize text-gray-700">{type}, {ayat} ayat</p>
                        </div>
                    </div>
                    <div>
                        <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 5v.01M12 12v.01M12 19v.01M12 6a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2z" />
                        </svg>
                    </div>
                </div>
           </NavLink>
        </React.Fragment>
    )
}