import React from "react";
import axios from "axios";
import { useParams } from "react-router";
import Isi from "../surat/isi";

export default function ListAyat() {
  const [ayat, setAyat] = React.useState([]);
  let {id} = useParams()
  const ayatHandle = async () => {
    try {
      let url = `https://api.npoint.io/99c279bb173a6e28359c/surat/${id}`;
      let respon = await axios.get(url);
      setAyat(respon.data);
      console.log(respon.data);
      console.log("sukses")
    } catch (err) {
        console.log(err)
    }
  };

  React.useEffect(()=>{
      ayatHandle()
  },[])
  return (
    <React.Fragment>
      <div className="bg-white py-8 px-7 h-full rounded-3xl">
        {ayat.map((index, key)=>(
          <Isi key={key}
          ar={index.ar}
          arti={index.id}
          no={index.nomor}
          ></Isi>
        ))}
      </div>
    </React.Fragment>
  );
}
