import "./App.css";
import React from "react";
import { BrowserRouter, Switch, Route, Link, Redirect } from "react-router-dom";
import AllSurat from "./pages/allsurat";
import Surat from "./pages/surat";

function App() {
  return (
    <BrowserRouter>
    <Switch>
      <Route path="/alquran" exact>
        <AllSurat></AllSurat>
      </Route>
      <Route path="/alquran/surah/:id" exact>
        <Surat></Surat>
      </Route>
      <Redirect from="/" to="/alquran"></Redirect>
    </Switch>
    </BrowserRouter>
  );
}

export default App;
