import React from "react";

export default function Isi({ar, arti, no}){
    return(
        <React.Fragment>
            <div className="text-right text-xl">
                <p className="mb-2 text-3xl">{ar}</p>
                <p className="mb-6"><span className="font-semibold">{no}.</span>  {arti}</p>
            </div>
        </React.Fragment>
    )
}