import axios from "axios";
import React from "react";
import { useHistory, useParams } from "react-router";
import Nama from "./nama";

export default function Nav2() {
  let history = useHistory();
  let { id } = useParams();
  const [note, setNote] = React.useState([]);
  const noteHandle = async () => {
    try {
      let url = `https://al-quran-8d642.firebaseio.com/data.json?print=pretty`;
      let respon = await axios.get(url);
      setNote(respon.data);
      console.log(respon.data);
      console.log("berhasil");
    } catch (err) {
      console.log(err);
    }
  };
  React.useEffect(() => {
    noteHandle();
  }, []);
  return (
    <div className="py-10 px-10 bg-green-500 text-white">
      <div className="flex justify-between ">
        <div className="bg-green-700 hover:bg-green-400 px-3 py-3 rounded-full">
          <svg
            onClick={() => {
              history.goBack("");
            }}
            xmlns="http://www.w3.org/2000/svg"
            className="h-6 w-6 font-semibold"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M15 19l-7-7 7-7"
            />
          </svg>
        </div>
        <div className="flex">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="h-6 w-6"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M5 5a2 2 0 012-2h10a2 2 0 012 2v16l-7-3.5L5 21V5z"
            />
          </svg>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="h-6 w-6 ml-6"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
            />
          </svg>
        </div>
      </div>
      <div className="mx-6">
        <Nama></Nama>
        {/* <p>id ke-{id}</p>
        {note?.map((i, unik)=>(
          <div key={unik}>
            <p>{i.nama}</p>
          </div>
        ))} */}
      </div>
    </div>
  );
}
