import React from "react";
import axios from "axios";
import Header from "../../component/header";
import Nav from "../../component/nav";
import List from "../../component/list";
export default function AllSurat() {
  const [allSurat, setAllSurat] = React.useState([]);
  const allSuratHandle = async () => {
    try {
      let url = `https://al-quran-8d642.firebaseio.com/data.json?print=pretty`;
      let result = await axios.get(url);
      console.log(result.data);
      setAllSurat(result.data);
      console.log("sukses");
    } catch (err) {
      console.log(err);
    }
  };
  React.useEffect(() => {
    allSuratHandle();
  }, []);

  return (
    <React.Fragment>
      <div className="h-screen w-full flex justify-center bg-gray-50">
        <div className="w-full md:w-2/5 lg:w-2/5 bg-white px-5 pt-10">
          <div className="h-1/6 w-full grid grid-cols-1 gap-2">
            <Header></Header>
            <Nav></Nav>
          </div>
          <div className="h-5/6 grid grid-cols-1 pt-5 w-full overflow-auto">
            {allSurat.map((surat, index) => (
              <List
                key={index}
                nama={surat.nama}
                ayat={surat.ayat}
                nomor={surat.nomor}
                type={surat.type}
              ></List>
            ))}
          </div>
          <div className=""></div>
        </div>
      </div>
    </React.Fragment>
  );
}
